import React, { createContext, useContext, useState, ReactNode, useEffect, useCallback } from 'react';
import { Scenario, SCENARIO_STORAGE_KEY, TIMESTAMPS_STORAGE_KEY } from './constants';

export interface AgentMessage {
  id: string;
  sender: string;
  content: string;
  timestamp: string;
  type: 'master' | 'bss' | 'resource' | 'assurance';
}

interface AgentChatContextType {
  messages: AgentMessage[];
  setMessages: (messages: AgentMessage[]) => void;
  loadScenario: (scenario: number) => void;
  currentScenario: number;
}

const AgentChatContext = createContext<AgentChatContextType | undefined>(undefined);

// 从 localStorage 加载时间戳
const loadTimestamps = (): Map<string, string> => {
  const savedTimestamps = localStorage.getItem(TIMESTAMPS_STORAGE_KEY);
  if (savedTimestamps && savedTimestamps !== '[]') {
    return new Map(JSON.parse(savedTimestamps));
  }
  return new Map();
};

// 保存时间戳到 localStorage
const saveTimestamps = (timestamps: Map<string, string>) => {
  localStorage.setItem(TIMESTAMPS_STORAGE_KEY, JSON.stringify(Array.from(timestamps.entries())));
};

// 预定义的对话场景（不包含时间戳）
