// 场景加载顺序
const SCENARIO_ORDER: number[] = Object.values(Scenario).filter(value => typeof(value) === 'number');

// 存储已生成的时间戳
const messageTimestamps = loadTimestamps();

export const AgentChatProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [messages, setMessages] = useState<AgentMessage[]>([]);
  const [currentScenario, setCurrentScenario] = useState<number>(Scenario.default);

  const loadScenario = useCallback((scenario: number) => {
    if (!SCENARIO_ORDER.includes(scenario)) {
      console.warn(`Scenario ${scenario} not found, using default scenario`);
      setCurrentScenario(Scenario.default);
      setMessages([]);
      return;
    }

    setCurrentScenario(scenario);
    
    // 收集所有需要显示的消息
    const allMessages: AgentMessage[] = [];
    
    // 遍历所有场景直到当前场景
    for (let i = 0; i <= scenario; i++) {
      const scenarioMessages = chatScenarios[i] || [];
      
      // 为每个消息添加时间戳
      scenarioMessages.forEach(msg => {
        if (!messageTimestamps.has(msg.id)) {
          // 如果是新消息，生成时间戳并保存
          const timestamp = new Date().toLocaleString();
          messageTimestamps.set(msg.id, timestamp);
        }
        
        allMessages.push({
          ...msg,
          timestamp: messageTimestamps.get(msg.id)!
        });
      });
    }

    saveTimestamps(messageTimestamps);
    setMessages(allMessages);
  }, []); // Empty dependency array since this function doesn't depend on any props or state

  // 在组件挂载时加载当前场景
  useEffect(() => {
    const currentScenario = Number(localStorage.getItem(SCENARIO_STORAGE_KEY));
    if (currentScenario) {
      loadScenario(currentScenario);
    } else {
      loadScenario(Scenario.default);
    }
  }, [loadScenario]);

  return (
    <AgentChatContext.Provider value={{ messages, setMessages, loadScenario, currentScenario }}>
      {children}
    </AgentChatContext.Provider>
  );
};

export const useAgentChat = () => {
  const context = useContext(AgentChatContext);
  if (context === undefined) {
    throw new Error('useAgentChat must be used within a AgentChatProvider');
  }
  return context;
}; 