import { UserSwitchOutlined } from '@ant-design/icons';
import type { Settings as LayoutSettings } from '@ant-design/pro-components';
import type { RunTimeLayoutConfig } from '@umijs/max';
import { history } from '@umijs/max';
import React from 'react';
import defaultSettings from '../config/defaultSettings';
import { errorConfig } from './requestErrorConfig';
import { SYSTEM_PRIMARY_COLORS } from './global';
import { AgentChatProvider } from './contexts/AgentChatContext';
import { Button, Select } from 'antd';
import { RESOURCE_AGENT_STATUS_STORAGE_KEY, RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, ResourceAgentStatus, Scenario, SCENARIO_STORAGE_KEY, TIMESTAMPS_STORAGE_KEY, USER_CHAT_STORAGE_KEY } from './contexts/constants';

const isDev = process.env.NODE_ENV === 'development';

/**
 * @see  https://umijs.org/zh-CN/plugins/plugin-initial-state
 * */
export async function getInitialState(): Promise<{
  settings?: Partial<LayoutSettings>;
  loading?: boolean;
  currentSystem?: 'user' | 'admin';
}> {


  // 根据当前路径决定系统类型
  const pathname = window.location.pathname;
  const isAdminPath = pathname.startsWith('/admin');
  const currentSystem = isAdminPath ? 'admin' : 'user';
  const settings = {...defaultSettings} as Partial<LayoutSettings>
  settings.title = currentSystem === 'user' ? 'User System' : 'Admin System';
  settings.colorPrimary = currentSystem === 'user' ? SYSTEM_PRIMARY_COLORS.user : SYSTEM_PRIMARY_COLORS.admin;

  return {
    settings,
    currentSystem,
  };
}

// ProLayout 支持的api https://procomponents.ant.design/components/layout
export const layout: RunTimeLayoutConfig = ({ initialState, setInitialState }) => {
  return {
    actionsRender: () => [
      <Button onClick={() => {
        localStorage.setItem(USER_CHAT_STORAGE_KEY, JSON.stringify([]));
        localStorage.setItem(SCENARIO_STORAGE_KEY, '0');
        localStorage.setItem(TIMESTAMPS_STORAGE_KEY, JSON.stringify(Array.from(new Map().entries())));
        localStorage.setItem(RESOURCE_AGENT_STATUS_STORAGE_KEY, ResourceAgentStatus.init.toString());
        let statusTimestamps = new Map();
        statusTimestamps.set(ResourceAgentStatus.init.toString(), new Date().toLocaleString());
        localStorage.setItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, JSON.stringify(Array.from(statusTimestamps.entries())));
        window.location.reload();
      }}>
        reset
      </Button>,
      <Select title='测试切换场景' style={{ minWidth: '160px' }} onChange={(value: string) => {
        localStorage.setItem(SCENARIO_STORAGE_KEY, value);
        window.location.reload();
      }}>
        {Object.values(Scenario).filter(value => typeof(value) === 'string').map(
          (value: string, index: number) => (
            <Select.Option key={index} value={index}>{value}</Select.Option>
          )
        )}
      </Select>
    ],
    bgLayoutImgList: [
      {
        src: 'https://mdn.alipayobjects.com/yuyan_qk0oxh/afts/img/D2LWSqNny4sAAAAAAAAAAAAAFl94AQBr',
        left: 85,
        bottom: 100,
        height: '303px',
      },
      {
        src: 'https://mdn.alipayobjects.com/yuyan_qk0oxh/afts/img/C2TWRpJpiC0AAAAAAAAAAAAAFl94AQBr',
        bottom: -68,
        right: -45,
        height: '303px',
      },
      {
        src: 'https://mdn.alipayobjects.com/yuyan_qk0oxh/afts/img/F6vSTbj8KpYAAAAAAAAAAAAAFl94AQBr',
        bottom: 0,
        left: 0,
        width: '331px',
      },
    ],
    // links: isDev
    //   ? [
    //       <Link key="openapi" to="/umi/plugin/openapi" target="_blank">
    //         <LinkOutlined />
    //         <span>OpenAPI 文档</span>
    //       </Link>,
    //     ]
    //   : [],
    menuHeaderRender: undefined,
    childrenRender: (children) => {
      return (
        <AgentChatProvider>
          {children}
        </AgentChatProvider>
      );
    },
    ...initialState?.settings,
    // 覆盖settings中的默认配置
    logo: (
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          cursor: 'pointer',
          padding: '0 8px',
        }}
      >
        <UserSwitchOutlined style={{ fontSize: '24px', color: initialState?.settings?.colorPrimary }} />
      </div>
    ),
    onMenuHeaderClick: () => {
      const newSystem = initialState?.currentSystem === 'user' ? 'admin' : 'user';
      const newSettings = {...defaultSettings}
      newSettings.title = newSystem === 'user' ? 'User System' : 'Admin System';
      newSettings.colorPrimary = newSystem === 'user' ? SYSTEM_PRIMARY_COLORS.user : SYSTEM_PRIMARY_COLORS.admin;

      setInitialState((preInitialState) => ({
        ...preInitialState,
        currentSystem: newSystem,
        settings: newSettings as Partial<LayoutSettings>,
      }));
      history.push(newSystem === 'user' ? '/user/chat' : '/admin');
    },
  };
};

/**
 * @name request 配置，可以配置错误处理
 * 它基于 axios 和 ahooks 的 useRequest 提供了一套统一的网络请求和错误处理方案。
 * @doc https://umijs.org/docs/max/request#配置
 */
export const request = {
  ...errorConfig,
};
